import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Paper from '@material-ui/core/Paper';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import Link from '@material-ui/core/Link';
import Typography from '@material-ui/core/Typography';
import AddressForm from './AddressForm';
import PaymentForm from './PaymentForm';
import Review from './Review';
import Axios from "axios";
import uniqid from "uniqid";
import { CartContext } from "../../components/Cart/CartContext"




const useStyles = makeStyles((theme) => ({
  layout: {
    width: 'auto',
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(2),
    [theme.breakpoints.up(600 + theme.spacing(2) * 2)]: {
      width: 600,
      marginLeft: 'auto',
      marginRight: 'auto',
    },
  },
  paper: {
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3),
    padding: theme.spacing(2),
    [theme.breakpoints.up(600 + theme.spacing(3) * 2)]: {
      marginTop: theme.spacing(6),
      marginBottom: theme.spacing(6),
      padding: theme.spacing(3),
    },
  },
  stepper: {
    padding: theme.spacing(3, 0, 5),
  },
  buttons: {
    display: 'flex',
    justifyContent: 'flex-end',
  },
  button: {
    marginTop: theme.spacing(3),
    marginLeft: theme.spacing(1),
  },
}));

const steps = ['Shipping address', 'Payment details', 'Review your order'];

export default function Checkout() {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);

  const [cart, dispatchCart] = React.useContext(CartContext)

  const prefix = Math.round(Math.random() * 10000)

  const products = cart.currentCart.map(product => {
    return{
      name: product.name,
      price: product.price,
      quantity: product.quantity,
      sellerEmail: product.user.email
    }
  })

  const [formData, setFormData] = React.useState({
    orderNumber: uniqid(prefix),
    firstName: '',
    lastName: '',
    address: '',
    secondAddress: '',
    city: '',
    state: '',
    zip: '',
    country: '',
    email: '',
    products: products,
  })

  const handleNext = () => {
    setActiveStep(activeStep + 1);

    if (activeStep === 2) {

      Axios.post(`${process.env.REACT_APP_BASEURL}/orders`, formData)
      .then(function (response) {
        // console.log(response.data);
      })
      .catch(function (error) {
        // console.log(error);
      });

      cart.currentCart.map(item => {
        let newQty = item.qty - item.quantity

        Axios.put(`${process.env.REACT_APP_BASEURL}/products/${item.id}`, {
          qty: newQty
        })
        .then(function (response) {
          // console.log(response);
        })
        .catch(function (error) {
          // console.log(error);
        });
      })
    }
  };

  const handleBack = () => {
    setActiveStep(activeStep - 1);
  };

  const {orderNumber, firstName, lastName, address, secondAddress, city, state, zip, country, email} = formData

  const values = {orderNumber, firstName, lastName, address, secondAddress, city, state, zip, country, email}

  const handleChange = input => e => {
    setFormData({...formData, [input]: e.target.value})
  }

  function getStepContent(step) {

  switch (step) {
    case 0:
      return <AddressForm handleChange={handleChange} values={values} />;
    case 1:
      return <PaymentForm />;
    case 2:
      return <Review values={values} />;
    default:
      throw new Error('Unknown step');
  }
}

  return (
    <React.Fragment>
      <CssBaseline />
      <main className={classes.layout}>
        <Paper className={classes.paper}>
          <Typography component="h1" variant="h4" align="center">
            Checkout
          </Typography>
          <Stepper activeStep={activeStep} className={classes.stepper}>
            {steps.map((label) => (
              <Step key={label}>
                <StepLabel>{label}</StepLabel>
              </Step>
            ))}
          </Stepper>
          <React.Fragment>
            {activeStep === steps.length ? (
              <React.Fragment>
                <Typography variant="h5" gutterBottom>
                  Thank you for your order.
                </Typography>
                <Typography variant="subtitle1">
                  Your order number is #{formData.orderNumber}. We have emailed your order confirmation, and will
                  send you an update when your order has shipped.
                </Typography>
              </React.Fragment>
            ) : (
              <React.Fragment>
                {getStepContent(activeStep)}
                <div className={classes.buttons}>
                  {activeStep !== 0 && (
                    <Button onClick={handleBack} className={classes.button}>
                      Back
                    </Button>
                  )}
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={handleNext}
                    className={classes.button}
                  >
                    {activeStep === steps.length - 1 ? 'Place order' : 'Next'}
                  </Button>
                </div>
              </React.Fragment>
            )}
          </React.Fragment>
        </Paper>
      </main>
    </React.Fragment>
  );
}
